body {
    font-family: RixMiniBus_Pro,'Roboto', sans-serif !important
}

html, body, #root, .App, #frontpage, #router{
    height: 100%;
}

h1 {
    font-size: 6em;
}

.divider {
    background: -webkit-linear-gradient(135deg, #C60C31 50%, #003478 50%);
    background: linear-gradient(-45deg, #C60C31 50%, #003478 50%);
    height: 9px;
    border: 0;
    display: block;
    width: 80%;
    margin: 7px;
}

.section-title {
    color: white;
    text-align: center;
    font-size: 62px;
    line-height: 72px;
}

.section hr {
    margin: auto;
    width: 20%;
}

.section-body {
    display: flex;
    flex-direction: column;
}

.card-chip {
    text-align: center;
}

.card-text {
    color: white;
    margin-top: 10px;
}

.card-chip img {
    height: 200px;
}

.section-text {
    color: white;
    background-color: #4E5153;
    padding: 16px;
    margin: 31px 0;
}

.section {
    padding: 40px 0px;
}

.card-rectangle {
    background-color: #373C40;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 20px;
    text-align: center;
    margin: 0 20px;
}

.card-rectangle .card-text {
    font-size: 50px
}

footer {
    color: white;
    background-color: #4b4b4b;
}

footer a {
    color: white;
}

nav {
    background-color: #4B4B4B;
    color: white;
}

#headerNavBar a{
    color:white;
}
#social-icons i {
    margin-right: 12px;
    font-size: 25px;
}

#headerNavBar a.nav-link {
    font-size: 13px;
}

#social-icons a{
    color: white;
}


div#twitch-embed {
    height: 100%;
    margin-top: 51px;
}
.headerImage {
    background-size: cover;
    background: url(/project-streamer/static/media/HeaderImage.92cac43f.png) no-repeat center;
    background-size: cover;
    height: 100%;
}

#btn-stream, #btn-channel {
    margin-right: auto;
}

#text-content {
    line-height: 1;
    color: white;
    top: 20%;
    left: 3%;
    /* margin: 176px 90px; */
    position: absolute;
}

#btn-group button {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 20px;
}

.banner-social {
    justify-content: space-around;
    padding: 43px;
    display: flex;
    flex-direction: row;
    color: white;
    bottom: 13%;
    position: absolute;
    background-color: #c60c31;
    width: 100%;
}

.banner-section {
    font-size: 40px;
}

.banner-social i {
    margin-right: 4px;
}

.banner-social a {
    color: white;
}

.banner-social a:hover {
    color: white;
    text-decoration: none;
}

@media only screen and (max-width: 853px) {
    .banner-social {
        padding: 28px;
    }
}

@media only screen and (max-width: 829px) {
    .banner-social {
        bottom: 0%;
    }
}

@media only screen and (max-width: 767px) {
    .banner-social {
        padding: 13px;
        bottom: -3%;
    }

    a.banner-section {
        font-size: 24px;
    }
}
#section-about {
    background-color: #373C40;
}

#section-about .chip-group {

}
#section-activity {
    background-color: #646464;
}
#section-contact{
    background-color: #373c40;
}



